:root {
  --primary-gradient: linear-gradient(90deg, #f12711 0%, #f5af19 100%);
  --secondary-gradient: linear-gradient(90deg, #1a1a2e 0%, #16213e 100%);
  --accent-color: #f5af19;
  --background-dark: #0f0f1e;
  --text-light: #ffffff;
  --text-dim: #8a8f98;
  --card-border: rgba(245, 175, 25, 0.3);
}

.App {
  background-color: var(--background-dark);
  min-height: 100vh;
  text-align: center;
}

.game-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  margin: 40px auto;
  padding: 0 20px;
}

.player-section {
  padding: 30px;
  border: 2px solid var(--card-border);
  border-radius: 16px;
  width: 300px;
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(10px);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.player-section:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 24px rgba(241, 39, 17, 0.1);
}

.card-space {
  height: 280px;
  width: 200px;
  border: 2px dashed var(--card-border);
  margin: 20px auto;
  border-radius: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
}

.card {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 4em;
  background: white;
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  transform-style: preserve-3d;
  transition: transform 0.6s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.card:hover {
  transform: rotateY(10deg) translateZ(20px);
}

.card.red {
  color: #f12711;
}

.card.black {
  color: #1a1a2e;
}

.game-controls {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.draw-button {
  padding: 15px 40px;
  font-size: 1.2em;
  background: var(--primary-gradient);
  border: none;
  border-radius: 12px;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.draw-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(241, 39, 17, 0.2);
}

.reset-button {
  padding: 12px 30px;
  font-size: 1em;
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid var(--card-border);
  border-radius: 12px;
  color: var(--accent-color);
  cursor: pointer;
  transition: all 0.3s ease;
}

.reset-button:hover {
  background: rgba(245, 175, 25, 0.1);
}

.game-status {
  margin: 15px 0;
  font-size: 1.2em;
  color: var(--accent-color);
  text-shadow: 0 0 10px rgba(245, 175, 25, 0.3);
}

h2 {
  color: var(--text-light);
  font-size: 1.8em;
  margin-bottom: 20px;
  background: var(--primary-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

p {
  color: var(--text-dim);
  font-size: 1.1em;
}

/* Add smooth card flip animation */
@keyframes flipCard {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(360deg);
  }
}

.card-space.flipping .card {
  animation: flipCard 0.6s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.betting-controls {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin: 2rem 0;
  padding: 20px;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 16px;
  backdrop-filter: blur(10px);
}

.betting-controls input {
  background: rgba(255, 255, 255, 0.05);
  border: 2px solid var(--card-border);
  border-radius: 12px;
  padding: 0.8rem 1rem;
  color: var(--text-light);
  width: 120px;
  font-size: 1.2em;
  text-align: center;
  transition: all 0.3s ease;
}

.betting-controls input:focus {
  outline: none;
  border-color: #f12711;
  box-shadow: 0 0 15px rgba(241, 39, 17, 0.2);
}

.bet-label {
  color: var(--accent-color);
  font-weight: bold;
  font-size: 1.2em;
  text-transform: uppercase;
  letter-spacing: 1px;
}
